import buttonSubmit from './buttonSubmit';
import checkInitialTodoList from './checkInitialTodoList';
import createListElement from './createListElement';
import createTodoList from './createTodoList';
import resetButton from './resetButton';
import searchInput from './searchInput';
import utils from './utils';

window.addEventListener('DOMContentLoaded', () => {
  buttonSubmit(utils, checkInitialTodoList, createTodoList);
  checkInitialTodoList(utils);
  createListElement(utils, createTodoList, buttonSubmit);
  createTodoList(utils, createListElement, searchInput);
  resetButton(createTodoList, searchInput);
  searchInput(createTodoList);
});
