function checkInitialTodoList(utils) {
  try {
    const todoList = utils.getTodoList();

    const isInvalidTodoList =
      !todoList ||
      !Array.isArray(todoList) ||
      !todoList.every(
        ({ id, content, isDone }) =>
          typeof id === 'string' && typeof content === 'string' && typeof isDone === 'boolean'
      );

    if (isInvalidTodoList) {
      utils.setTodoList([]);
    }
  } catch (e) {
    utils.setTodoList([]);
  }
}

export default checkInitialTodoList;
